<script>
// import mockData from '@/config/mock.js';
// import VueMasonryWall from 'vue-masonry-wall';
import * as http from "@/common/utils/request.js";
export default {
  data() {
    return {
      // projectData: mockData.projectData,
      // projectData1: [],
      tokenData: [],
    };
  },
  // components: {
  //   VueMasonryWall,
  // },
  methods: {
    toTokenDetail(id) {
      this.$router.push(`/token/${id}`);
    },
    async getProjectDetail() {
      const params = {
        projectId: this.$route.params.projectID,
      };
      try {
        const res = await http.fetchGet("/api/project", params);
        console.log(res);
        this.tokenData = res.data?.data?.all_tokens;
      } catch (error) {
        console.log(error);
      }
    },
  },
  created() {
    this.getProjectDetail();
  },
  computed: {
    routerlist() {
      return [
        { to: "/", name: "Home" },
        { to: "/projects", name: "Projects" },
        {
          to: `/projectDetail/${this.$route.params.projectID}`,
          name: "ProjectDetail",
        },
        {
          name: "Gallery",
        },
      ];
    },
  },
};
</script>

